@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes expand {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes shrink {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}

@keyframes rotate1 {
  0% {
    transform: rotate3d(1, 1, 1, 0deg);
  }
  100% {
    transform: rotate3d(1, 1, 1, 360deg);
  }
}

@keyframes rotate2 {
  0% {
    transform: rotate3d(1, 1, 1, 360deg);
  }
  100% {
    transform: rotate3d(1, 1, 1, 0deg);
  }
}

@keyframes rotate3 {
  0% {
    transform: rotate3d(1, 1, 1, 40deg);
  }
  100% {
    transform: rotate3d(1, 1, 1, 360deg);
  }
}

@keyframes rotate4 {
  0% {
    transform: rotate3d(1, 1, 1, 0deg);
  }
  100% {
    transform: rotate3d(1, 1, 1, 720deg);
  }
}

@keyframes slide-up {
  0% {
    bottom: -150px;
  }
  100% {
    bottom: 0;
  }
}

@keyframes slide-right {
  0% {
    left: -200px;
  }
  100% {
    left: 0;
  }
}

@keyframes slide-left {
  0% {
    right: -200px;
  }
  100% {
    right: 0;
  }
}

@keyframes slide-down {
  0% {
    top: -150px;
  }
  100% {
    top: 0;
  }
}

@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
    border: 1px solid red;
  }
  25%,
  75% {
    transform: translateX(-10px);
    border: 1px solid red;
  }
  50% {
    transform: translateX(10px);
    border: 1px solid red;
  }
}

@keyframes breathe {
  0%,
  50% {
    transform: scale(0.8);
  }
  25%,
  75% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
