@import "animation.css";

.sidebar-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  background-color: var(--accent1-1);
  font-family: var(--lato);
  line-height: 50px;
  opacity: 0;
  animation: show 3s ease-out 2s forwards;
  border-bottom-right-radius: 20px;
  box-shadow: 5px 5px 10px -2px gray;
  z-index: 2;
}

.list-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10%;
  font-weight: 800;
  font-size: 1.4rem;
  letter-spacing: 1px;
  background-color: #24251c;
  color: var(--primary1);
  cursor: pointer;
  border-bottom: 1px solid gray;
}

.sidebar-list-container {
  width: 100%;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.list-chair {
  color: var(--primary1);
  width: 100%;
  text-align: center;
  text-decoration: none;
  font-size: 1rem;
}

.list-chair:hover {
  width: 100%;
  justify-self: center;
  cursor: pointer;
  font-weight: 600;
  border-radius: 10px;
  background-color: var(--primary1);
  color: var(--accent1-1);
  box-shadow: 0 0 8px 2px black;
  font-size: 1.1rem;
}

@keyframes spread {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}
